export default {
	name: 'uni-swipe-action',
	props: {
		clickData:{
			type:[Object,Array]
		},
		isOpened: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		autoClose: {
			type: Boolean,
			default: true
		},
		options: Array
	},
	watch: {
		isOpened(newValue, oldValue) {
			this.isShowBtn = newValue ? true : false;
			this.endMove();
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId: elId,
			isShowBtn: false,
			transformX: 'translateX(0px)'
		}
	},
	created() {
		this.direction = ''
		this.startX = 0
		this.startY = 0
		this.btnGroupWidth = 0
		this.isMoving = false
	},






	onReady() {
		this.getSize()
	},

	methods: {
		getSize() {
			uni.createSelectorQuery().in(this).select(`#${this.elId}`).boundingClientRect().exec((ret) => {
				this.btnGroupWidth = ret[0].width;
			});
			if (this.isOpened === true) {
				this.isShowBtn = true;
				this.endMove();
			}
		},
		bindClickBtn(item, index) {
			this.$emit('click', {
				text: item.text,
				style: item.style,
				index: index,
				data:this.clickData
			})
		},
		bindClickCont(e) {
			if (this.isShowBtn && this.autoClose === true) {
				this.isShowBtn = false;
				this.endMove();
			}
		},
		touchStart(event) {
			this.startX = event.touches[0].pageX;
			this.startY = event.touches[0].pageY;
		},
		touchMove(event) {
			if (this.direction === 'Y' || this.disabled === true) {
				return;
			}
			var moveY = event.touches[0].pageY - this.startY,
				moveX = event.touches[0].pageX - this.startX;
			if (!this.isMoving && Math.abs(moveY) > Math.abs(moveX)) { //纵向滑动
				this.direction = 'Y';
				return;
			}
			this.direction = moveX > 0 ? 'right' : 'left';
			this.isMoving = true;
		},
		touchEnd(event) {
			this.isMoving = false;
			if (this.direction !== 'right' && this.direction !== 'left') {
				this.direction = '';
				return;
			}
			if (this.direction == 'right') {
				this.isShowBtn = false
			} else {
				this.isShowBtn = true
			}
			this.endMove()
		},
		endMove() {
			if (this.direction === 'Y' || this.disabled === true) {
				this.direction = '';
				return;
			}
			if (this.isShowBtn) {
				this.transformX = `translateX(${-this.btnGroupWidth}px)`;
				this.$emit('opened');
			} else {
				this.transformX = 'translateX(0px)';
				this.$emit('closed');
			}
			this.direction = '';
		},
		close() {
			this.isShowBtn = false
			this.endMove()
		}
	}
}
